<template>
  <div class="statisticalEcharts">
    <div class="tabAll">
      <div></div>
      <div>
<!--           v-show="isShowTimeTab(index)" -->
        <div
          v-for="(item, index) in timeTab"
          :key="index"
          v-show="isShowTimeTab(index)"
          :class="{ isActive: index == tTIndex }"
          @click="changeTimeTab(index)"
        >
          {{ item }}
        </div>
      </div>
    </div>
    <div
      style="
        display: flex;
        flex-direction: column;
        position: relative;
        flex-grow: 1;
      "
    >
      <div
        style="
          display: flex;
          align-items: center;
          justify-content: space-between;
        "
      >
        <div class="eleTab">
          <div
            v-for="(item, index) in eleTab"
            :key="index"
            :class="{ isActive: index == eleIndex }"
            @click="changeEleTab(index)"
          >
            {{ item }}
          </div>
        </div>
        <div v-show="eleIndex == 2" class="energyType">
          <div
            v-for="(item, index) in energyArry"
            :key="index"
            @click="energyTypeChange(index)"
          >
            <i class="check" v-show="energyIndex == index"></i>
            <i class="noCheck" v-show="energyIndex != index"></i>
            {{ item }}
          </div>
        </div>
        <el-select
          class="transparentInner"
          v-model="value"
          placeholder="请选择"
          @change="changeOption"
        >
          <el-option
            v-for="item in options"
            :key="item.elecEqmId"
            :label="item.elecEqmName"
            :value="item.elecEqmId"
          >
          </el-option>
        </el-select>
        <!-- <div class="date">
          <img
            style="width: 0.16rem; height: 0.13rem"
            src="@/assets/run_powerStaic/yearL.png"
            alt=""
          />
          <img
            style="width: 0.08rem"
            src="@/assets/run_powerStaic/monthL.png"
            alt=""
          />
          <span>2022-05</span>
          <img
            style="width: 0.08rem"
            src="@/assets/run_powerStaic/monthR.png"
            alt=""
          />
          <img
            style="width: 0.16rem; height: 0.13rem"
            src="@/assets/run_powerStaic/yearR.png"
            alt=""
          />
        </div> -->
      </div>
      <div class="echart" ref="dlEchart" ></div>
    </div>
  </div>
</template>
<script>
import { charts, elecEqmList } from "@/network/powerStaic";
export default {
  props: ["elecStationId"],
  data() {
    return {
      timeTab: ["天", "月", "年"],
      tTIndex: 0,
      eleTab: ["总电能", "功率", "峰电"],
      eleIndex: 0,
      options: [],
      value: "",
      energyArry: ["总电能", "尖峰平谷"],
      energyIndex: 0,
      legendArry: ["当日", "昨日"],
      legendArryAll: [
        ["当日", "昨日"],
        ["当月", "上月"],
        ["今年", "去年"],
      ],
    };
  },
  watch: {

    elecStationId(val) {
        // this.setEchart()
      this.elecStationIdSend = val;
      this.elecEqmListFun(); //获取设备列表
      this.chartsFun(); //获取图表数据

    },
  },
  mounted() {
    this.setEchart()
    this.energyTypeChange(0)
      this.chartsFun();
      this.chartType1()
  },
  methods: {
    isShowTimeTab(index) {
      let show = true;
      //判断选择峰电的时候（index=2）,只显示月（index=1）
      if (this.eleIndex == 2) {
        if (index != 1) {
          return false;
        }
      }
      return show;
    },
    chartType1() {
      let xAxisArry = [55];
      let yAxisArry = [60];
      let yAxisArry1 = [44];
      //总电能
      // this.chartContent.valuesN.forEach((item) => {
      //   xAxisArry.push(item.key);
      //   yAxisArry.push(item.value);
      // });
      // this.chartContent.valuesY.forEach((item) => {
      //   yAxisArry1.push(item.value);
      // });
      this.setEchart("single", xAxisArry, yAxisArry, yAxisArry1);
    },
    chartType2() {
      let xAxisArry = [140,320,330,360,500,600,500,300,200,100];
      let yAxisArry = [140,320,330,360,500,600,500,300,200,100];
      let yAxisArry1 = [140,320,330,360,500,600,500,300,200,100];
      let yAxisArry2 = [140,320,330,360,500,600,500,300,200,100];
      let yAxisArry3 = [140,320,330,360,500,600,500,300,200,100];
      let yAxisArry4 = [140,320,330,360,500,600,500,300,200,100];
      let yAxisArry5 = [140,320,330,360,500,600,500,300,200,100];
      let yAxisArry6 = [140,320,330,360,500,600,500,300,200,100];
      let yAxisArry7 = [140,320,330,360,500,600,500,300,200,100];
      //尖电
      // this.chartContent.valuesNJ.forEach((item) => {
      //   xAxisArry.push(item.key);
      //   yAxisArry.push(item.value);
      // });
      // this.chartContent.valuesYJ.forEach((item) => {
      //   yAxisArry1.push(item.value);
      // });
      // //峰电
      // this.chartContent.valuesNF.forEach((item) => {
      //   yAxisArry2.push(item.value);
      // });
      // this.chartContent.valuesYF.forEach((item) => {
      //   yAxisArry3.push(item.value);
      // });
      // //平电
      // this.chartContent.valuesNP.forEach((item) => {
      //   yAxisArry4.push(item.value);
      // });
      // this.chartContent.valuesYP.forEach((item) => {
      //   yAxisArry5.push(item.value);
      // });
      // //谷电
      // this.chartContent.valuesNG.forEach((item) => {
      //   yAxisArry6.push(item.value);
      // });
      // this.chartContent.valuesYG.forEach((item) => {
      //   yAxisArry7.push(item.value);
      // });
      this.setEchart("more", xAxisArry, {
        当日尖电: yAxisArry,
        昨日尖电: yAxisArry1,
        当日峰电: yAxisArry2,
        昨日峰电: yAxisArry3,
        当日平电: yAxisArry4,
        昨日平电: yAxisArry5,
        当日谷电: yAxisArry6,
        昨日谷电: yAxisArry7,
      });
    },
    energyTypeChange(index) {
      this.energyIndex = index;
      if (index == 0) {
        this.chartType1();
      } else if (index == 1) {
        this.chartType2();
      }
      // else if (index == 2) {
      //   //峰电
      //   this.chartContent.valuesNF.forEach((item) => {
      //     xAxisArry.push(item.key);
      //     yAxisArry.push(item.value);
      //   });
      //   this.chartContent.valuesYF.forEach((item) => {
      //     yAxisArry1.push(item.value);
      //   });
      // } else if (index == 3) {
      //   //谷电
      //   this.chartContent.valuesNP.forEach((item) => {
      //     xAxisArry.push(item.key);
      //     yAxisArry.push(item.value);
      //   });
      //   this.chartContent.valuesYP.forEach((item) => {
      //     yAxisArry1.push(item.value);
      //   });
      // } else if (index == 4) {
      //   //谷电
      //   this.chartContent.valuesNG.forEach((item) => {
      //     xAxisArry.push(item.key);
      //     yAxisArry.push(item.value);
      //   });
      //   this.chartContent.valuesYG.forEach((item) => {
      //     yAxisArry1.push(item.value);
      //   });
      // }
    },
    changeTab(index) {
      this.tabIndex = index;
    },
    //"天", "月", "年" 切换
    changeTimeTab(index) {
      this.tTIndex = index;
      this.legendArry = this.legendArryAll[this.tTIndex]; //改变图列名称
      this.chartsFun();
    },
    //"总电能", "功率", "峰电" 切换
    changeEleTab(index) {
      this.energyIndex = 0;
      this.eleIndex = index;
      // 选择峰电的时候只能选择月
      if (index == 2) {
        this.tTIndex = 1;
        this.legendArry = this.legendArryAll[this.tTIndex]; //改变图列名称
      }
      this.chartsFun();
    },
    changeOption() {
      this.chartsFun();
    },
    elecEqmListFun() {

      let errcode =0;

      if (errcode == 0) {
        content.unshift({
          elecEqmId: "",
          elecEqmName: "全部",
        });
        this.options = content;
      }
      // elecEqmList(this.elecStationIdSend)
      //   .then((res) => {
      //     const { content, errcode, errmsg } = res;
      //     if (errcode == 0) {
      //       content.unshift({
      //         elecEqmId: "",
      //         elecEqmName: "全部",
      //       });
      //       this.options = content;
      //     }
      //   })
      //   .catch((err) => {});
    },
    chartsFun() {
      this.$parent.$parent.elecStationLoad = true;
      // this.energyIndex = 1
      if (this.energyIndex == 1) {
        this.chartType2()
      } else {
        this.chartType1()
      }

      // charts(this.eleIndex, this.tTIndex, this.value, this.elecStationIdSend)
      //   .then((res) => {
      //     const { content, errcode, errmsg } = res;
      //     if (errcode == 0) {
      //       this.chartContent = content;
      //       if (this.energyIndex == 1) {
      //         this.chartType2()
      //       } else {
      //        this.chartType1()
      //       }
      //     }
      //     this.$parent.$parent.elecStationLoad = false;
      //   })
      //   .catch((err) => {
      //     this.$parent.$parent.elecStationLoad = false;
      //   });
    },
    setEchart(state, xAxisArry, yAxisArry, yAxisArry1) {
      let chart = this.$refs.dlEchart;
      let myChart = this.$echarts.init(chart);
      myChart.clear();
      let series = [];
      if (state == "single") {
        series = [
          {
            name: this.legendArry[0],
            type: "line",
            z: 2,
            lineStyle: {
              color: "#0044FF",
            },
            areaStyle: {
              color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: "rgba(4, 0, 255, 0.52)",
                },
                {
                  offset: 1,
                  color: "rgba(0, 132, 255, 0.51)",
                },
              ]),
            },
            smooth: true,
            emphasis: {
              focus: "series",
            },
            // data: yAxisArry,
            data:[100,200,450,1000,630,610,500,600,654,901]
          },
          {
            name: this.legendArry[1],
            type: "line",
            z: 1,
            lineStyle: {
              color: "#00FF9D",
            },
            areaStyle: {
              color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: "rgba(0, 255, 157, 0.56)",
                },
                {
                  offset: 1,
                  color: "rgba(0, 238, 255, 0.48)",
                },
              ]),
            },
            smooth: true,
            emphasis: {
              focus: "series",
            },
            // data: yAxisArry1,
            data:[100,200,450,330,540,960,741,500,610,300]
          },
        ];
      } else {
        let areaColor = [
          [84, 112, 198],
          [145, 204, 117],
          [250, 200, 88],
          [238, 102, 102],
          [115, 192, 222],
          [58, 159, 112],
          [252, 132, 82],
          [154, 96, 180],
        ];
        let keys = Object.keys(yAxisArry);
        keys.forEach((item, index) => {
          series.push({
            name: item,
            type: "line",
            z: 1,
            lineStyle: {
              color: `rgba(${areaColor[index][0]},${areaColor[index][1]},${areaColor[index][2]})`,
            },
            areaStyle: {
              color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: `rgba(${areaColor[index][0]},${areaColor[index][1]},${areaColor[index][2]},0.56)`,
                },
                {
                  offset: 1,
                  color: `rgba(${areaColor[index][0]},${areaColor[index][1]},${areaColor[index][2]},0.48)`,
                },
              ]),
            },
            smooth: true,
            emphasis: {
              focus: "series",
            },
            // data: yAxisArry[item],
            data:[100,200,450,610,500,600,1110,210,690,880]
          });
        });
      }
      let option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        legend: {
          textStyle: {
            color: "#fff",
          },
        },
        grid: {
          left: "3%",
          right: "2%",
          bottom: "2%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            // data: xAxisArry,
            data:[100,200,450,450,960,1000,660,500,600,700],
            axisLabel: {
              show: true,
              textStyle: {
                color: "#ffffff",
              },
            },
            axisTick: {
              show: false,
            },
          },
        ],
        yAxis: [
          {
            name: "单位（kw）",
            nameLocation: "end",
            nameTextStyle: {
              color: "#fff",
              padding: [0, 10, 0, 0],
            },
            nameGap: 15,
            type: "value",
            axisLabel: {
              show: true,
              textStyle: {
                color: "#ffffff",
              },
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: ["rgba(255, 255, 255, 0.5)"],
                type: "dashed",
              },
            },
          },
        ],
        series: series,
      };
      myChart.setOption(option);
      window.addEventListener("resize", function () {
        myChart.resize();
      });
    },
  },
};
</script>
<style scoped lang="less">
.isActive {
  background: #0056dc !important;
  font-size: 0.2rem;
}
.statisticalEcharts {
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.normalBk {
  background: rgba(0, 54, 129, 1);
  color: #ffffff;
  cursor: pointer;
}
.tabAll {
  display: flex;
  justify-content: space-between;
  padding: 0 0.41rem 0 0.22rem;
  padding-top: 0.12rem;
  align-items: flex-end;
  border-bottom: 1px solid rgba(34, 175, 237, 0.5);
  > div {
    display: flex;
    > div {
      .normalBk();
    }
  }
  > div:last-child {
    margin-bottom: 0.07rem;
  }
  > div:first-child > div {
    font-size: 0.18rem;
    padding: 0 0.32rem;
    height: 0.4rem;
    border-radius: 0.04rem 0.04rem 0 0;
    margin: 0 0.03rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  > div:last-child > div:first-child {
    border-radius: 0.04rem 0 0 0.04rem;
  }
  > div:last-child > div:last-child {
    border-radius: 0rem 0.04rem 0.04rem 0;
  }
  > div:last-child > div {
    font-size: 0.17rem;
    padding: 0.05rem 0.09rem;
    border-radius: 0.04rem 0.04rem 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0;
    margin: 0 0.01rem;
  }
}
.isTabActive {
  background: #0056dc;
}
.eleTab {
  display: flex;
  margin-top: 0.19rem;
  margin-left: 0.32rem;
  > div {
    font-size: 0.16rem;
    padding: 0.05rem 0.09rem;
    margin: 0 1px;
    .normalBk();
  }
  > div:first-child {
    border-radius: 0.04rem 0 0 0.04rem;
  }
  > div:last-child {
    border-radius: 0rem 0.04rem 0.04rem 0;
  }
}
.date {
  display: flex;
  align-items: center;
  color: #22afed;
  font-size: 0.17rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 0.19rem;
  img {
    margin: 0 0.1rem;
    cursor: pointer;
  }
  span {
    margin: 0 0.19rem;
  }
}
.echart {
  flex-grow: 1;
}
/deep/ .el-input__inner {
  background-color: rgba(0, 55, 146, 0.2);
  border: 1px solid rgba(34, 175, 237, 0.5);
  color: #ffffff;
  height: 35px;
  line-height: 35px;
  font-size: 0.16rem;
}
.el-select {
  width: 150px;
  margin-top: 0.19rem;
  margin-right: 0.32rem;
}
.energyType {
  display: flex;
  font-size: 0.18rem;
  > div {
    margin: 0 0.17rem;
    cursor: pointer;
  }
}
.check {
  display: inline-block;
  width: 0.13rem;
  height: 0.13rem;
  background: url("@/assets/run_powerStaic/check.png");
  background-size: 100% 100%;
}
.noCheck {
  display: inline-block;
  width: 0.13rem;
  height: 0.13rem;
  background: url("@/assets/run_powerStaic/no-check.png");
  background-size: 100% 100%;
}
</style>